import { Component, OnInit, Input, ViewChild, ViewChildren, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, NgForm } from '@angular/forms';
import { TankConfigurationService } from 'src/app/shared/services/tank-configuration.service';
import { TankConfiguration, TankType, TankRoofType } from 'src/app/shared/models/tank-configuration';
import { Subscription } from 'rxjs';
import { AuditEvent } from 'src/app/shared/models';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { AuditService } from 'src/app/shared/services/audit.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AttachmentService } from 'src/app/shared/services/attachment.service';
import { ToastrService } from 'ngx-toastr';
import { saveAs } from 'file-saver';

@Component({
  selector: 'app-tank-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit, OnDestroy {

  activeTank: TankConfiguration = new TankConfiguration();


  loadAuditDataSub: Subscription;
  tankSelectSub: Subscription;
  companyId: number;
  showHistory = true;

  loadingData = false;
  showAuditProgress: boolean;

  audits: AuditEvent[];
  auditEventDetail: string[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public service: TankConfigurationService,
    private auditService: AuditService,
    private router: Router,
    private modalService: NgbModal,
    private attachmentService: AttachmentService,
    private toastr: ToastrService,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.route.params.subscribe(params => this.companyId = params['id']);
    this.activeTank = null;

    this.tankSelectSub = this.service.onTankConfigurationSelected.subscribe(res => {
      if (res) {
        this.activeTank = res.tank;
        this.showHistory = false;
        this.audits = [];
        this.loadingData = false;
      }
    });
  }

  ngOnDestroy(): void {
    if (this.tankSelectSub) { this.tankSelectSub.unsubscribe(); }
  }


  edit(tank) {
    this.router.navigate(['edit', tank.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showAuditProgress = true;
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.getAuditTrails('tank', this.activeTank.id.toString())
        .subscribe(res => {
          this.audits = res.events;
          this.showAuditProgress = false;
        });
    }
  }

  clone(tank: TankConfiguration) {
    this.router.navigate(['clone', tank.id], {relativeTo: this.route});
  }

  open(content, eventId) {
    this.auditService.getAuditEventDetail(eventId).subscribe(
      response => {
        this.auditEventDetail = response.changeDetails;
        this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
      .result.then((result) => {
        console.log(result);
      }, (reason) => {
        console.log('Err!', reason);
      });
      },
      error => {
        console.log(error);
      }
    );
  }

  confirm(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title', centered: true })
    .result.then((result) => {
        this.loadingData = true;
        this.service.delete(this.activeTank.id)
          .subscribe(
            res => {
              if (res) {
                this.service.loadTankConfigurationData(0, 6);
                this.toastr.success('Company successfully deleted !', 'Success', { timeOut: 4000 });
              } else {
                // this.toastr.error(res.errorMessage, 'Failed', { timeOut: 4000});
              }
              this.loadingData = false;
            },
            error => {
              this.toastr.error(error.errorMessage, 'Failed', { timeOut: 30000});
              this.loadingData = false;
            }
          );
    }, (reason) => {
      console.log('model reason : ' + reason);
    });
  }

  getTankTypeText(): string {
    const res = TankType[this.activeTank.tankType];
    return res;
  }
  getTankRoofTypeText(): string {
    return TankRoofType[this.activeTank.tankRoofType];
  }


  download(id: number) {
    this.attachmentService.download(id).subscribe(res => {
      if (res) {
        saveAs('data:application/text;base64,' + res.content, res.name);
      }
    });
  }

}
