import { Component, OnInit } from '@angular/core';
import { Subscription, Observable } from 'rxjs';
import { TankConfiguration } from 'src/app/shared/models/tank-configuration';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { map, switchMap, startWith, debounceTime } from 'rxjs/operators';
import { TankConfigurationService } from 'src/app/shared/services/tank-configuration.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-tank-left-pane',
  templateUrl: './left-pane.component.html',
  styleUrls: ['./left-pane.component.scss']
})
export class LeftPaneComponent implements OnInit {

  advancedSearchSub: Subscription;

  tanks: TankConfiguration[];
  // filteredCompanies: Company[];
  loadDataSub: Subscription;
  selectedAction = 'All';
  loadingCollection = false;
  advancedSearchApplied = false;

  page = 1;
  pageSize = 6;
  collectionSize: number;
  
  searchControl: FormControl = new FormControl();

  filterAction: string[] = ['All', 'Active', 'Inactive'];

  advancedSearchForm: FormGroup;

  public userInput = new FormControl();

  constructor(private service: TankConfigurationService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private fb: FormBuilder) {
  }


  ngOnInit(): void {
    this.loadingCollection = true;
    this.setAdvancedSearchForm();

    this.loadDataSub = this.service.loadTankConfigurationData(this.page, this.pageSize)
      .subscribe(res => {
        this.tanks = res.content;
        // this.filteredCompanies = this.tanks;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
    });

    this.searchControl.valueChanges
      .pipe(debounceTime(200))
      .subscribe(value => {
        this.filerData(value);
    });

    // Listen for company change
    this.advancedSearchSub = this.service.onAdvancedSearchApplied.subscribe(res => {
      if (res) {
        this.tanks = res.companies.content;
        this.collectionSize = res.companies.totalElements;
      }
    });

  }

  filerData(val) {
    let query = '?name=' + val;
    if (this.selectedAction.toLocaleLowerCase() !== 'all') {
      query = query + '&status=' + this.selectedAction.toUpperCase();
    }
    this.service.performAdvancedSearch(query);
  }

  filerDataWithStatus(val) {
    let query = '';
    if (this.selectedAction.toLocaleLowerCase() !== 'all') {
      query = query + '?status=' + this.selectedAction.toUpperCase();
    }
    this.service.performAdvancedSearch(query);
  }

  getTankConfigurationById(tankId) {
    this.service.getTankConfigurationById(tankId)
      .subscribe(res => {

      }, err => {
        console.log(err);
      });
  }

  getPageFromService(page: number) {
    this.loadingCollection = true;
    this.service.loadTankConfigurationData(page, this.pageSize)
      .subscribe(res => {
        this.tanks = res.content;
        this.collectionSize = res.totalElements;
       this.loadingCollection = false;
       this.page = page;
    });
  }

  filter(action) {
    this.selectedAction = action;
    this.filerDataWithStatus(action.toString());
  }

  add() {
    this.router.navigate(['add'], { relativeTo: this.route });
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      registrationNumber: '',
      status: ''
    });
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
    .result.then((result) => {
      console.log(result);
    }, (reason) => {
      console.log('Err!', reason);
    });
  }

  performAdvancedSearch(event) {
    this.loadingCollection = true;
    this.modalService.dismissAll('close clicked');
    this.advancedSearchApplied = true;
  }

  clearSearchCriteria() {
    this.advancedSearchApplied = false;
    this.filerData('');
  }

   showPagination() {
   }

}
